/* Navbar.css */
.navbar {
    position: sticky;
    top: 0;
    background-color: white;
    z-index: 1000;
    box-shadow: 0 2px 5px rgba(0,0,0,0.1);
    padding: 10px 20px;
  }
  
  .navbar ul {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .navbar ul li {
    margin: 0 15px;
  }
  
  .navbar ul li button {
    background: none;
    border: none;
    color: black;
    font-size: 16px;
    font-weight: 500;
    cursor: pointer;
    padding: 10px 20px;
    border-radius: 5px;
    transition: background-color 0.3s, color 0.3s;
  }
  
  .navbar ul li button:hover
   {
    background-color: #f0f0f5;
    outline: none;
    /* .navbar ul li button:focus */
  }
  
  .navbar ul li button.active {
    color: rgb(255, 59, 48); /* Apple blue */
    /* font-weight: bold; */
  }
  @media only screen and (max-width: 600px) {
   .navbar{
    display: none;
   }
  }